function parsePromiseData(data) {
	//console.log(data);
	var output = {};
	if (data) {
		Object.keys(data).map(function (key) {
			var val = data[key];
			val.key = key;
			return val;
		}).sort(function (a, b) {
			return a.timestamp - b.timestamp;
		}).forEach(function (node) {
			var promiseid = node.promiseid;
			if (!(promiseid in output)) {
				output[promiseid] = {
					//
					promise_id: false,
					author: false,
					title: 'Untitled Promise',
					description: '...',
					level: 1,
					completed: false,
					//
					started: false,
					finished: false,
					due: false,
					lastActive: 0,
					//
					links: {},
					comments: {}
				};
			}
			if (output[promiseid].lastActive < node.timestamp) {
				output[promiseid].lastActive = node.timestamp;
			}
			switch (node.type) {
				case 'edit':
					if (!output[promiseid].started) {
						output[promiseid].started = node.timestamp;
					}
					if (node.author && !output[promiseid].author) {
						output[promiseid].author = node.author;
					}
					if (node.title) {
						output[promiseid].title = node.title;
						// Add Comment
					}
					if (node.description) {
						output[promiseid].description = node.description;
						// Add Comment
						output[promiseid].comments[node.key] = {
							author: node.author,
							timestamp: node.timestamp,
							text: 'Set description: ' + node.description,
							generated: true
						};
					}
					if (node.level) {
						output[promiseid].level = node.level;
						// Add Comment
					}
					if (node.due) {
						output[promiseid].due = node.due;
						// Add Comment
					}
					break;
				case 'comment':
					output[promiseid].comments[node.key] = {
						author: node.author,
						timestamp: node.timestamp,
						text: node.text
					};
					break;
				case 'link':
					var linkid = node.linkid;
					if (!(linkid in output[promiseid].links)) {
						output[promiseid].links[linkid] = {
							linkid: linkid
						};
						output[promiseid].comments[node.key] = {
							author: node.author,
							timestamp: node.timestamp,
							text: 'Added link: ' + (node.name || 'untitled link') + '.',
							generated: true
						};
					}
					if (node.url) {
						output[promiseid].links[linkid].url = node.url;
					}
					if (node.name) {
						output[promiseid].links[linkid].name = node.name;
					}
					if (node.remove) {
						delete output[promiseid].links[linkid];
						// Add Comment
					}
					break;
				case 'complete':
					output[promiseid].completed = node.completed;
					output[promiseid].finished = node.timestamp;
					// Add Comment
					output[promiseid].comments[node.key] = {
						author: node.author,
						timestamp: node.timestamp,
						text: 'Marked as ' + (node.completed ? 'complete' : 'incomplete') + '.',
						generated: true
					};
					break;
				default:
					break;
			}
		});
	}
	return output;
}

function promptLinkDataThen(message, _callback, inPrefill) {
	var prefill = inPrefill || {};
	vex.dialog.prompt({
		message: message,
		value: prefill.url,
		callback: function callback(url) {
			if (url) {
				vex.dialog.prompt({
					message: 'What is the name of this link?',
					value: prefill.name,
					callback: function callback(name) {
						if (url && name) {
							_callback(url, name);
						}
					}
				});
			}
		}
	});
}

function checkUserPermission(tid, team, members, user) {
	/*return new Promise((resolve, reject) => {
 	});*/

	if (isMentor()) {
		console.log('in mentor mode');
		document.body.classList.add('mentoring');
		var editableNodes = document.querySelectorAll('[contenteditable=true]');
		for (var n = 0; n < editableNodes.length; n++) {
			editableNodes[n].setAttribute('contenteditable', false);
		}
	}

	var uid = database.getCurrentUser().uid;
	if (uid in members || isMentor()) {
		if (Object.keys(team).length > 0) {
			mainPromiseTab(tid, team, members, user);
		}
	} else if (params.code) {
		vex.dialog.confirm({
			message: 'Do you want to join ' + (team.name || 'this team') + '?',
			buttons: [$.extend({}, vex.dialog.buttons.YES, { text: 'Yes' }), $.extend({}, vex.dialog.buttons.NO, { text: 'No' })],
			callback: function callback(yes) {
				if (yes) {
					database.joinTeam(tid, uid, params.code).then(function (res) {
						if (res.success) {
							vex.dialog.alert({
								message: 'Congratulations, you just joined ' + (team.name || 'your new team') + '!'
							});
							if (Object.keys(team).length > 0) {
								renderTeamCharter(tid, team, members);
							}
						} else {
							window.location = window.location.origin + '/me.html';
						}
					});
				} else {
					window.location = window.location.origin + '/me.html';
				}
			}
		});
	} else {
		window.location = window.location.origin + '/me.html';
	}
}

function isMentor() {
	return MENTOR_MODE;
}

function reportErrorToUser(err) {
	console.error(err);
	vex.dialog.alert(err + '');
}

function getQueryParams(qs) {
	qs = qs.split('+').join(' ');
	var params = {},
	    tokens,
	    re = /[?&]?([^=]+)=([^&]*)/g;
	while (tokens = re.exec(qs)) {
		params[decodeURIComponent(tokens[1])] = decodeURIComponent(tokens[2]);
	}
	return params;
}

function fillText(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].innerText = text;
	}
}

function fillSrc(className, text) {
	var spans = document.getElementsByClassName(className);
	for (var s = 0; s < spans.length; s++) {
		spans[s].src = text;
	}
}

var cancels = 0;
function initWithTeamCode(_callback2) {
	if (TEAM_ID) {
		_callback2(TEAM_ID);
	} else {
		vex.dialog.prompt({
			message: 'Enter your team code:',
			callback: function callback(code) {
				if (code) {
					_callback2(code);
				} else if (cancels >= 1) {
					window.location = './';
				} else {
					cancels++;
					initWithTeamCode(_callback2);
				}
			}
		});
	}
}

